@import url(https://fonts.googleapis.com/css?family=Raleway);
input {
    font-size: 2em;
    color: white;
    transition-duration: 2s;
    border-radius: 5px;
    cursor: pointer;
}

@media (min-width: 1024px) {
    input {
        font-size: 1.2em;
    }
}

span{
  font-size: 3em;
  transition-duration: 2s;
}

@media (min-width: 1024px) {
  span{
    font-size: 1.7em;
  }
}

i {
    font-size: 3em;
    margin-left: 10%;
    margin-right: 10%;
    transition-duration: 2s;
    cursor: pointer;
  }

@media (min-width: 1024px){
    i {
        font-size: 2.5em;

      }
}

span {
    transition-duration: 2s;
}
.Container {
  font-family: 'Raleway', sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  height: 100vh;
  transition-duration: 2s;
}


.QuoteBox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 40%;
  padding: 1.5%;
  width: 70%;
  max-height: 32%;
  border-radius: 5px;
  background-color: white;
  
}


.ButtonsIconContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.Icons {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.NewQuote {
  display: -webkit-inline-flex;
  display: inline-flex;
}

/* for viewports of 1024px or wider */

@media (min-width: 1024px) {
  .Container {
    font-family: 'Raleway', sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    height: 100vh;
    transition-duration: 2s;
  }
  
  
  .QuoteBox {
    margin-top: 10%;
    width: 32%;  
  }
  
  

  

}

